'use client';

import { useState } from 'react';
import { useForm } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import * as z from 'zod';
import { Button } from '@/components/ui/button';
import {
  Form,
  FormControl,
  FormField,
  FormItem,
  FormLabel,
  FormMessage,
} from '@/components/ui/form';
import { Textarea } from '@/components/ui/textarea';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Avatar, AvatarFallback, AvatarImage } from '@/components/ui/avatar';
import { Progress } from '@/components/ui/progress';
import { Bot, Sparkles, ThumbsUp, Wand2 } from 'lucide-react';
import { useToast } from '@/hooks/use-toast';
import { Skeleton } from '@/components/ui/skeleton';
import type { MenteeProfile, MatchResult } from '@/lib/types';

type GetMentorSuggestions = (menteeProfile: MenteeProfile) => Promise<{
  success: boolean;
  suggestions?: MatchResult[];
  error?: string;
}>;

const formSchema = z.object({
  menteeInterests: z.string().min(20, {
    message: 'Please describe your interests in at least 20 characters.',
  }),
  menteeGoals: z.string().min(20, {
    message: 'Please describe your goals in at least 20 characters.',
  }),
  menteeBackground: z.string().min(20, {
    message: 'Please describe your background in at least 20 characters.',
  }),
});

export function MatchingClient({ getMentorSuggestions }: { getMentorSuggestions: GetMentorSuggestions }) {
  const [isLoading, setIsLoading] = useState(false);
  const [suggestions, setSuggestions] = useState<MatchResult[]>([]);
  const { toast } = useToast();

  const form = useForm<z.infer<typeof formSchema>>({
    resolver: zodResolver(formSchema),
    defaultValues: {
      menteeInterests: '',
      menteeGoals: '',
      menteeBackground: '',
    },
  });

  async function onSubmit(values: z.infer<typeof formSchema>) {
    setIsLoading(true);
    setSuggestions([]);
    const result = await getMentorSuggestions(values);
    setIsLoading(false);

    if (result.success && result.suggestions) {
      setSuggestions(result.suggestions);
    } else {
      toast({
        variant: 'destructive',
        title: 'An error occurred',
        description: result.error || 'Could not fetch mentor suggestions.',
      });
    }
  }
  
  const LoadingSkeleton = () => (
    <div className="space-y-4">
        {[1, 2, 3].map((i) => (
            <Card key={i}>
                <CardHeader className="flex flex-row items-center gap-4">
                    <Skeleton className="h-16 w-16 rounded-full" />
                    <div className="flex-1 space-y-2">
                        <Skeleton className="h-6 w-3/4" />
                        <Skeleton className="h-4 w-1/2" />
                    </div>
                </CardHeader>
                <CardContent>
                    <Skeleton className="h-4 w-full mb-2" />
                    <Skeleton className="h-4 w-full mb-2" />
                    <Skeleton className="h-4 w-3/4" />
                </CardContent>
            </Card>
        ))}
    </div>
  );

  return (
    <div className="grid grid-cols-1 lg:grid-cols-3 gap-8 items-start">
      <Card className="lg:col-span-1 lg:sticky lg:top-20">
        <CardHeader>
          <CardTitle>Your Profile</CardTitle>
          <CardDescription>The more detail you provide, the better the matches will be.</CardDescription>
        </CardHeader>
        <CardContent>
          <Form {...form}>
            <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-6">
              <FormField
                control={form.control}
                name="menteeInterests"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel>Your Interests</FormLabel>
                    <FormControl>
                      <Textarea placeholder="e.g., Sustainable agriculture, coding, entrepreneurship, web design..." {...field} />
                    </FormControl>
                    <FormMessage />
                  </FormItem>
                )}
              />
              <FormField
                control={form.control}
                name="menteeGoals"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel>Your Goals</FormLabel>
                    <FormControl>
                      <Textarea placeholder="e.g., Start a tech company, get into a good university, improve my farm's output..." {...field} />
                    </FormControl>
                    <FormMessage />
                  </FormItem>
                )}
              />
              <FormField
                control={form.control}
                name="menteeBackground"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel>Your Background</FormLabel>
                    <FormControl>
                      <Textarea placeholder="e.g., Student in computer science, own a small family farm, recently graduated..." {...field} />
                    </FormControl>
                    <FormMessage />
                  </FormItem>
                )}
              />
              <Button type="submit" disabled={isLoading} className="w-full">
                {isLoading ? (
                  <>
                    <Sparkles className="mr-2 h-4 w-4 animate-spin" />
                    Finding Mentors...
                  </>
                ) : (
                   <>
                    <Wand2 className="mr-2 h-4 w-4" />
                     Find My Mentors
                   </>
                )}
              </Button>
            </form>
          </Form>
        </CardContent>
      </Card>
      <div className="lg:col-span-2">
        <h2 className="text-2xl font-bold font-headline mb-4">Suggested Mentors</h2>
        {isLoading && <LoadingSkeleton />}
        {!isLoading && suggestions.length === 0 && (
          <div className="flex flex-col items-center justify-center text-center border-2 border-dashed rounded-lg p-12 min-h-[400px]">
            <Bot className="h-12 w-12 text-muted-foreground" />
            <h3 className="text-lg font-semibold mt-4">Ready to find your match?</h3>
            <p className="mt-2 text-muted-foreground max-w-sm">Fill out your profile on the left, and our AI will generate personalized mentor suggestions for you here.</p>
          </div>
        )}
        <div className="space-y-4">
          {suggestions.map((s) => (
            <Card key={s.mentorId} className="overflow-hidden transition-all hover:shadow-md">
              <CardHeader className="flex flex-row items-start gap-4 p-4">
                <Avatar className="w-16 h-16 border">
                  <AvatarImage src={s.avatarUrl} alt={s.name} />
                  <AvatarFallback>{s.name?.charAt(0) || 'M'}</AvatarFallback>
                </Avatar>
                <div className="flex-1">
                  <div className="flex justify-between items-start">
                    <div>
                        <CardTitle className="font-headline">{s.name}</CardTitle>
                        <CardDescription>{s.title}</CardDescription>
                    </div>
                     <Button size="sm">Request Session</Button>
                  </div>
                  <div className="mt-2 flex items-center gap-2">
                     <div className="flex items-center gap-1 text-sm font-semibold text-primary">
                        <ThumbsUp className="h-4 w-4" />
                        <span>{s.compatibilityScore}% Match</span>
                    </div>
                    <Progress value={s.compatibilityScore} className="w-32 h-2" />
                  </div>
                </div>
              </CardHeader>
              <CardContent className="px-4 pb-4">
                <div className="bg-secondary p-3 rounded-md border">
                    <h4 className="font-semibold text-sm mb-1 flex items-center gap-2">
                        <Bot className="w-4 h-4 text-primary" />
                        AI Rationale
                    </h4>
                    <p className="text-sm text-muted-foreground">{s.rationale}</p>
                </div>
              </CardContent>
            </Card>
          ))}
        </div>
      </div>
    </div>
  );
}
